.card {
  background: #fff;
  border-radius: 2px;
  height: 150px;
  margin: 1rem;
  position: relative;
  width: 150px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  width: 100%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}

.card > .img-container > img {
    width: 100%;
}

.img-container:hover {
    margin-left: -5px;
    margin-top: -5px;
    height: 160px;
    width: 160px;
    -webkit-box-shadow: 0 8px 14px #999, 0 8px 14px #999;
            box-shadow: 0 8px 14px #999, 0 8px 14px #999;
}



